.headerPanel {
  float: left;
  background-color: white;
  height: 48px;
  width: 100%;
  position: fixed;
  z-index: 99;
  display: flex;
  flex-flow: row;
  align-items: stretch;
  justify-content: space-between;
}

.headerCaption {
  display: flex;
  flex-flow: row;
  align-items: center;
  font-size: 34px;
  font-weight: 400;
  letter-spacing: 0.25px;
  margin-left: 24px;
}

.headerCaption a {
  color: #bc5a97;
  text-decoration: none;
  font-weight: 700;
  font-size: 18px;

  padding: 16px;
}

.headerBtn {
  color: #b9bec9;
  margin-left: 16px;
  display: flex;
  flex-flow: row wrap;
}

.headerBtn a {
  color: #b9bec9;
  text-decoration: none;
  font-weight: 700;
  font-size: 14px;
  padding: 16px;
}
